@import url(https://fonts.googleapis.com/css2?family=Alata&family=Montserrat:wght@400;600&family=Roboto&family=Roboto+Mono&display=swap&family=Raleway:wght@300&family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
	--text-primary-color: #f6f4e6;
	--text-secondary-color: #fd793c;
	--accent-color: #fddb3a;
	--bg-1-color: #161513;
	--bg-2-color: #49239a;
	
}

h1 {
	color: #fddb3a;
	color: var(--accent-color);
}

p {
	color: #f6f4e6;
	color: var(--text-primary-color);
	font-family: 'Roboto', sans-serif;
}
@media (min-width: 1480px) {
	#landing--container video {
		max-width: 100vw;
	}
}

@media (min-width: 576px) {
	#header-content {
		margin-right: 0!important;
		margin-left: 12vw!important;
		max-width: -webkit-fit-content!important;
		max-width: -moz-fit-content!important;
		max-width: fit-content!important;
		text-align: left!important;
	}
}

#landing--container {
	display: flex;
	height: 100vh;
}

#landing--container video {
	position: fixed;
	z-index: -1;
}

#landing--container h1 {
	font-size: 4rem!important;
	font-weight: 200;
	font-family: 'Raleway', sans-serif;
}

#landing--container h2 {
	color: var(--text-primary-color);
	font-family: 'Raleway', sans-serif;
}

#header-content {
	text-align: center;
	
	margin: 20vh auto;
	background-color: rgba(65, 68, 75, 0.4);
	max-width: 95vw;
	min-width: 1vw;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
}

#social-buttons {
	position: absolute;
    color: white;
    bottom: 0;
    left: 0;
    font-size: 2rem;
}

#social-buttons * {
 transition: all .1s ease-in;
	margin: 5px;
}

#social-buttons *:hover {
	cursor: pointer;
	color: var(--accent-color);
}

@media (min-width: 768px) { 
	.job-details {
		width: 60%;
	}
}

hr {
	border-color: var(--text-secondary-color);
}

.job-details h2 {
	color: var(--text-primary-color);
}

.job-details h5 {
	color: var(--accent-color);
}

.job--thumbnail {
	text-align: center;
}

.job--thumbnail img {
	width: 100%;
    border-radius: 25px;
}
#skills-page {
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
}
@media (min-width: 576px) { 
	#skills-chart  {
		font-size: 30px!important;
	}
}


@media (min-width: 992px) { 
	#skills-chart  {
		max-width: 80%!important;
		font-size: 30px!important;
	}

	.skill-container {
		padding-right: 30%;
	}
}

#skills-chart {
	white-space: nowrap;
	font-size: 6vw;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: 30px auto;
    color: var(--text-secondary-color);
    font-family: 'Raleway', sans-serif;
	border-color: var(--bg-2-color)!important;
}

h2 {
	margin: 15px;
	color: var(--text-primary-color);;
}

.skill-container span{
 transition: all .1s ease-in;
}

.skill-container span:hover {
	transform: scale(1.2);
}

.meter-bar {
	width: 20%;
	height: 75%;
	background-color: var(--accent-color);
}

.border-left, .border-right {
	border-color: var(--text-secondary-color)!important;
}
#site--content {
	background-color: var(--bg-1-color);
}

#site--content h1{
	font-size: 4rem;
	text-align: center;
	font-weight: 300;
}
